*{
    font-family: 'Manrope', sans-serif;
}

:root{
    /* ### Primary */

---LightCyan: hsl(193, 38%, 86%);
---NeonGreen: hsl(150, 100%, 66%);

/* ### Neutral */

---GrayishBlue: hsl(217, 19%, 38%);
---DarkGrayishBlue: hsl(217, 19%, 24%);
}
.container{
    background-color: var(---DarkGrayishBlue);
    width: 640px;
    height: 400px;
    position: absolute;
    left: 50%;
    transform: translate(-50%,0);
    margin-top: 120px;
    border-radius: 1rem;
}

@media screen and (max-width:768px){
    .container{
        max-width: 90%;
    }

}
.index{
 font-size: 1.5rem;
 text-transform: uppercase;
 color: var(---NeonGreen);
 text-align: center;
 letter-spacing: 0.5rem;
 padding-top: 20px;
}
@media screen and (max-width:768px){
    .index{
        font-size: 14px;
        letter-spacing: 0.3rem;
    }
}
.text{
    font-size: 2rem;
    color: var(---LightCyan);
    text-align: center;
    padding: 2rem 5rem;
    height: 100px;
}
@media screen and (max-width:768px){
    .text{
        font-size: 1.5rem;
        padding: 3rem;
    }
}
.dividerD{
    height: 44px;
    width: 640px;
    background: url(images/pattern-divider-desktop.svg) no-repeat ;
    background-position: center;
    margin-bottom: 25px;
}
@media screen and (max-width:768px){
    .dividerD{
        display: none;
    }
}

.dividerM{
    display: none;
}
@media screen and (max-width:768px){
    .dividerD{
        display: block;
        height: 44px;
        width: 100%;
        background: url(images/pattern-divider-mobile.svg) no-repeat ;
        background-position: center;
    }
}

.btn{
    background: url(images/icon-dice.svg), var(---NeonGreen);
    background-size: 50%;
    background-repeat: no-repeat;
    background-position: center;
    height: 100px;
    width: 100px;
    border-radius: 50%;
    margin-left: 272px;
}

@media screen and (max-width:768px){
    .btn{
        height: 20vw;
        width: 20vw;
        margin-left: 35vw;
    }
}

.btn:hover{
    box-shadow: 0 5px 15px var(---NeonGreen);
}


